<script setup>
import { onMounted, ref, computed } from "vue";
import ImageModal from '@/components/ImageModal.vue';
import axios from "axios";
import { useUserStore } from "@/stores/user";
import { log } from "@/utils/logger";

const userData = useUserStore();

const fileInput = ref(null);
const default_str = ["Mengambil Data...", "Izin Ditolak", "Gagal Memuat"];
const ipData = ref({ ipv4: default_str[0], ipv6: default_str[0] });
const gpsLocation = ref({ lat: default_str[0], lon: default_str[0] });
const ipLocation = ref({ city: default_str[0], country: default_str[0], lat: "", lon: "" });
const gpsMapUrl = ref("");
const ipMapUrl = ref("");
const attendanceRecorded = ref(false);
const selectedLocation = ref("");
const locations = ref([]);
const absensiStatusArray = ref([]);
const isLoadingAbsensiStatus = ref(true);
const showModal = ref(false);
const imageSrc = ref('');
const ZOOM_LEVEL = 12;

const dataLoaded = computed(() => {
  return ![
    gpsLocation.value.lat,
    gpsLocation.value.lon,
    ipData.value.ipv4
  ].some(val => default_str.includes(val));
});

const tooFar = computed(() => {
  return (selectedLocation.value?.distance ?? 99999) > 1000;
});

const absensiStatus = computed(() => {
  const latest = absensiStatusArray.value[0];
  if (!latest) return "IN";
  const hourIn = latest.hour_in ? new Date(latest.hour_in) : null;
  const now = new Date();
  const timeDiffHours = hourIn ? (now - hourIn) / (1000 * 60 * 60) : null;
  if (latest.hour_out && timeDiffHours >= 16 || timeDiffHours >= 16) {
    return "IN";
  }
  if (!latest.hour_out || latest.hour_out === "00:00:00") {
    return "OUT";
  }
  return "DONE";
});

const fetchAbsensiStatus = async () => {
  const responseStatus = await fetch(`${userData.apiBaseUrl}/absensi-status?username=${userData.username}`);
  absensiStatusArray.value = await responseStatus.json();
  if (absensiStatusArray.value.length > 0) {
    absensiStatusArray.value[0].foto_in = `${userData.baseUrl}/uploads/absensi/${absensiStatusArray.value[0].foto_in}`;
    absensiStatusArray.value[0].foto_out = `${userData.baseUrl}/uploads/absensi/${absensiStatusArray.value[0].foto_out}`;
  }
  isLoadingAbsensiStatus.value = false;
};

const fetchGPSLocation = () => {
  return new Promise((resolve) => {
    if ("geolocation" in navigator) {
      try {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            gpsLocation.value.lat = position.coords.latitude.toFixed(6);
            gpsLocation.value.lon = position.coords.longitude.toFixed(6);
            gpsMapUrl.value = `https://static-maps.yandex.ru/1.x/?lang=en-US&ll=${gpsLocation.value.lon},${gpsLocation.value.lat}&size=450,300&z=${ZOOM_LEVEL}&l=map&pt=${gpsLocation.value.lon},${gpsLocation.value.lat},pm2rdm`;
            resolve(true);
          },
          (error) => {
            console.warn("‚ùå Geolocation permission denied or error:", error.message);
            gpsLocation.value.lat = default_str[1];
            gpsLocation.value.lon = default_str[1];
            resolve(false);
          },
          {
            enableHighAccuracy: true,
            timeout: 5000,
            maximumAge: 0,
          }
        );
      } catch (err) {
        console.error("‚ùå Unexpected GPS error:", err);
        gpsLocation.value.lat = default_str[2];
        gpsLocation.value.lon = default_str[2];
        resolve(false);
      }
    } else {
      gpsLocation.value.lat = default_str[2];
      gpsLocation.value.lon = default_str[2];
      resolve(false);
    }
  });
};

function getDistanceFromLatLonInKm(lat1, lon1, lat2, lon2) {
  const R = 6371;
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a =
    Math.sin(dLat / 2) ** 2 +
    Math.cos(lat1 * Math.PI / 180) *
    Math.cos(lat2 * Math.PI / 180) *
    Math.sin(dLon / 2) ** 2;
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c * 1000; // meter
  return isNaN(distance) ? 0 : distance.toFixed(2);
}

const fetchLokasi = async () => {
  const responseLokasi = await axios.get(`${userData.apiBaseUrl}/hr-lokasi`);
  locations.value = responseLokasi.data.map((item) => {
    const lat = item.latitude ?? null;
    const lon = item.longitude ?? null;
    const distance = (lat !== null && lon !== null && gpsLocation.value.lat && gpsLocation.value.lon)
      ? getDistanceFromLatLonInKm(gpsLocation.value.lat, gpsLocation.value.lon, lat, lon)
      : Infinity;
    return { nama: item.nama ?? "", lat, lon, distance };
  });

  locations.value.sort((a, b) => a.distance - b.distance);
  if (locations.value.length > 0) {
    selectedLocation.value = locations.value[0];
  }
};

const fetchIp = async () => {
  try {
    const responseIPv4 = await axios.get("https://www.transkon-rent.com/api/ip");
    ipData.value.ipv4 = responseIPv4.data.ip;
    // console.log(responseIPv4.data);
  } catch (error) {
    console.error("Error fetching IP:", error);
    ipData.value.ipv4 = default_str[2];
  }
};

const fetchIPLocation = async () => {
  try {
    const response = await fetch("http://ip-api.com/json/");
    const data = await response.json();
    ipLocation.value = {
      city: data.city || "Tidak diketahui",
      country: data.country || "Tidak diketahui",
      lat: data.lat ? data.lat.toFixed(6) : "",
      lon: data.lon ? data.lon.toFixed(6) : "",
    };
    if (data.lon && data.lat) {
      ipMapUrl.value = `https://static-maps.yandex.ru/1.x/?lang=en-US&ll=${data.lon},${data.lat}&size=450,300&z=${ZOOM_LEVEL}&l=map&pt=${data.lon},${data.lat},pm2rdm`;
    }
  } catch (error) {
    console.error("Error fetching IP geolocation:", error);
    ipLocation.value.city = "Gagal memuat";
    ipLocation.value.country = "Gagal memuat";
  }
};

const openModal = (src) => {
  imageSrc.value = src;
  showModal.value = true;
};

const closeModal = () => {
  showModal.value = false;
};

const openCamera = () => {
  if (!selectedLocation.value) {
    alert("Silakan pilih lokasi absen terlebih dahulu!");
    return;
  }
  document.getElementById("cameraInput").click();
};

const getDeviceInfo = () => {
  const dimension = `${window.innerWidth}x${window.innerHeight}`;
  const userAgent = navigator.userAgent;

  const match = userAgent.match(/\(([^)]+)\)/);
  let device = match ? match[1] : `?${userAgent}`;

  // Cut if too long (safe for VARCHAR(255))
  if (device.length > 250) {
    device = device.substring(0, 250);
  }

  return {
    dimension,
    device
  };
};


const handlePhoto = async (event) => {
  const file = event.target.files[0];
  if (!file) return;
  try {
    const {dimension, device} = getDeviceInfo();
    const formData = new FormData();
    formData.append("foto", file);
    formData.append("id", absensiStatus.value == "IN" ? null : absensiStatusArray.value[0].id);
    formData.append("status", absensiStatus.value);
    formData.append("username", userData.username);
    formData.append("lokasi", selectedLocation.value.nama);
    formData.append("jarak", selectedLocation.value.distance);
    formData.append("long", gpsLocation.value.lon);
    formData.append("lang", gpsLocation.value.lat);
    formData.append("ip", ipData.value.ipv4);
    // log("Device Info : ", {dim: dimension, device: device});
    formData.append("dim", dimension);
    formData.append("device", device);

    const response = await axios.post("https://www.transkon-rent.com/api/absensi", formData, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });

    const result = response.data;
    if (result.status === "success") {
      attendanceRecorded.value = true;
      window.location.reload();
    } else {
      console.error("Attendance failed:", result.message);
    }
  } catch (err) {
    console.error("Upload error:", err);
  }
};

onMounted(async () => {
  try {
    console.log("1. Meminta izin GPS...");
    const gpsSuccess = await fetchGPSLocation();
    if (!gpsSuccess) {
      console.warn("‚ö†Ô∏è Lokasi gagal dimuat. Melanjutkan tanpa GPS...");
    }
    await Promise.all([
      fetchLokasi(),
      fetchIp(),
      fetchAbsensiStatus()
    ]);
    console.log("‚úÖ Semua data berhasil dimuat.");
  } catch (err) {
    console.error("üí• Unhandled error in onMounted:", err);
  }
});
function formatDate(dateString) {
  const months = [
    "Januari", "Februari", "Maret", "April", "Mei", "Juni",
    "Juli", "Agustus", "September", "Oktober", "November", "Desember"
  ];

  const date = new Date(dateString);
  const day = date.getDate();
  const month = months[date.getMonth()];
  const year = date.getFullYear();
  
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');
  const seconds = String(date.getSeconds()).padStart(2, '0');

  return `${day} ${month} ${year} ${hours}:${minutes}:${seconds}`;
}

</script>

<template>
  <div class="app-container">
    <!-- Main Content -->
    <main class="main-content">
      <h1 class="app-title">Absensi Karyawan</h1>
      <p class="app-subtitle"></p>
      <!-- Attendance Form -->
      <div class="container-global attendance-card">
        <div class="form-group">
          <!-- <label for="location" class="form-label">Lokasi Absensi</label>
          <select v-model="selectedLocation" id="location" class="form-select">
            <option value="" disabled selected>Loading...</option>
            <option v-for="(location, index) in locations" :key="index" :value="location">
              {{ location.nama }}
            </option>
          </select> -->
          <p v-if="locations.length > 0" id="location" class="form-control-plaintext">
            {{ locations[0].nama }}
            <!-- {{ userData.baseUrl + absensiStatusArray[0].foto_in }} -->
          </p>
          <p v-else class="form-control-plaintext text-muted">Loading location...</p>
        </div>
        <div class="mb-4" v-if="absensiStatus === 'OUT' || absensiStatus === 'DONE'">
          <div>{{ formatDate(absensiStatusArray[0].hour_in) }}</div>
          <VButton @click="openModal(absensiStatusArray[0].foto_in)" class="btn btn-danger">Photo In</VButton>
          <ImageModal
            :imageSrc="imageSrc"
            :showModal="showModal"
            @close="closeModal"
          />
        </div>

        <div class="mb-4" v-if="absensiStatus === 'DONE'">
          <div>{{ formatDate(absensiStatusArray[0]?.hour_out) }}</div>
          <VButton @click="openModal(absensiStatusArray[0].foto_out)" class="btn btn-danger">Photo Out</VButton>
          <ImageModal
            :imageSrc="imageSrc"
            :showModal="showModal"
            @close="closeModal"
          />
        </div>
        <!-- Attendance Button -->
        <div class="action-group">
          <input type="file" accept="image/*" capture="environment" @change="handlePhoto" id="cameraInput" style="display: none;" />
            <Button 
              @click="openCamera"
              class="primary-btn" 
              :disabled="!selectedLocation || absensiStatus === 'DONE' || isLoadingAbsensiStatus || !dataLoaded || tooFar"
            >
              <i class="fa-solid fa-camera"></i>
              <span v-if="isLoadingAbsensiStatus">Loading...</span>
              <span v-else-if="!dataLoaded">Gagal Memuat</span>
              <span v-else-if="tooFar">Anda Diluar Zona Absensi</span>
              <span v-else>{{ absensiStatus === 'DONE' ? 'Sudah Absen' : absensiStatus === 'OUT' ? 'Absen OUT' : 'Absen IN' }}</span>          
            </Button>
        </div>

        <!-- Attendance Confirmation -->
        <div v-if="attendanceRecorded" class="confirmation-message">
          <i class="fa-solid fa-check-circle success-icon"></i>
          <span>Absensi berhasil direkam!</span>
        </div>
      </div>

      <!-- Announcement Section -->
      <div class="container-global announcement-card">
        <div class="announcement-header">
          <i class="fa-solid fa-bullhorn announcement-icon"></i>
          <h3>TRJA Memo</h3>
        </div>
        <div class="announcement-content">
          <img src="/src/assets/images/avatars/eid.jpg" 
               alt="Gambar Lebaran" 
               class="announcement-image" />
          <p class="announcement-text">Selamat Hari Raya Idul Fitri 1445 H</p>
        </div>
      </div>

      <!-- Location Information -->
      <div class="container-global info-section-edit">
        <!-- GPS Info -->
        <div class="info-card">
          <div class="info-header">
            <i class="fa-solid fa-map-marker-alt"></i>
            <h4>Lokasi GPS Anda</h4>
          </div>
          <div class="info-content">
            <div class="info-row">
              <span class="info-label">Latitude:</span>
              <span class="info-value">{{ gpsLocation.lat }}</span>
            </div>
            <div class="info-row">
              <span class="info-label">Longitude:</span>
              <span class="info-value">{{ gpsLocation.lon }}</span>
            </div>
            <div class="info-row">
              <span class="info-label">Jarak:</span>
              <span v-if="locations.length > 0" class="info-value">
                {{ locations[0].distance + " Meter" }}
              </span>
              <span v-else class="info-value text-muted">
                Menghitung Jarak..
              </span>
            </div>
            <div class="info-row">
              <span class="info-label">IPv4:</span>
              <span class="info-value">{{ ipData.ipv4 }}</span>
            </div>
            <!-- <div class="info-row">
              <span class="info-label">IPv6:</span>
              <span class="info-value">{{ ipData.ipv6 }}</span>
            </div> -->
            <img :src="gpsMapUrl" alt="GPS Map" v-if="gpsMapUrl" class="map-image" />
          </div>
        </div>
      </div>
    </main>
  </div>
</template>


<style>
/* Import Font Awesome CSS */
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');



body {
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 100vh;
            font-family: Arial, sans-serif;
        }
        
        .main-content {
            text-align: center;
        }
        
        .app-title {
            font-size: 2.5rem;
            margin-bottom: 0.5rem;

        }
        
        .app-subtitle {
            font-size: 1.5rem;
            margin-top: 0;
        }
/* Base Styles */
:root {
  --primary-color: #3498db;
  --secondary-color: #2980b9;
  --success-color: #2ecc71;
  --danger-color: #e74c3c;
  --warning-color: #f39c12;
  --light-color: #ecf0f1;
  --dark-color: #2c3e50;
  --text-color: #333;
  --text-light: #7f8c8d;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: var(--text-color);
  background-color: #f5f7fa;
}

.hidden-input {
  display: none;
}

/* Layout */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 100%;
  overflow-x: hidden;
}

.main-content {
  flex: 1;
  padding: 1rem;
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
}

/* Header */
.app-header {
  background-color: var(--primary-color);
  color: white;
  padding: 1.5rem 1rem;
  text-align: center;
  box-shadow: var(--box-shadow);
}

.app-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.app-subtitle {
  font-size: 0.9rem;
  opacity: 0.9;
}

/* Cards */
.attendance-card,
.announcement-card,
.info-card {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

/* Form Elements */
.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  text-align: left;
}

.form-select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  font-size: 1rem;
  transition: var(--transition);
  background-color: white;
}

.form-select:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

/* Buttons */
.primary-btn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  min-width: 120px; 
  height: 40px; 
  font-size: 14px; 
}

.primary-btn:hover {
  transform: translateY(-1px);
}

.primary-btn:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}

/* Confirmation Message */
.confirmation-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  color: var(--success-color);
  font-weight: 500;
  margin-top: 1rem;
  padding: 0.75rem;
  background-color: rgba(46, 204, 113, 0.1);
  border-radius: var(--border-radius);
}

.success-icon {
  font-size: 1.2rem;
}

/* Announcement Section */
.announcement-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}

.announcement-header h3 {
  font-size: 1.2rem;
  color: var(--dark-color);
}

.announcement-icon {
  color: var(--warning-color);
  font-size: 1.2rem;
}

.announcement-image {
  width: 100%;      /* Responsif, mengisi lebar container */
  max-width: 800px; /* Batas maksimal lebar */
  height: auto;     /* Menjaga rasio aspek */
  border-radius: 8px; /* Sudut melengkung */
  margin-bottom: 1rem;
}

.announcement-text {
  text-align: center;
  font-weight: 500;
  color: var(--text-color);
}

/* Info Sections */
.info-section {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.info-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}

.info-header h4 {
  font-size: 1.1rem;
  color: var(--dark-color);
}

.info-header i {
  color: var(--primary-color);
  font-size: 1.1rem;
}

.info-content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
}

.info-label {
  font-weight: 500;
  color: var(--text-light);
}

.info-value {
  font-weight: 500;
  color: var(--dark-color);
  text-align: right;
}

.map-image {
  width: 100%;
  border-radius: var(--border-radius);
  margin-top: 1rem;
  border: 1px solid #eee;
}

/* Footer */
.app-footer {
  background-color: var(--dark-color);
  color: white;
  text-align: center;
  padding: 1.5rem 1rem;
  margin-top: 2rem;
  
}

.app-footer p {
  margin-bottom: 0.5rem;
}

.footer-copyright {
  font-size: 0.8rem;
  opacity: 0.8;
}

/* Responsive Adjustments */
@media (min-width: 768px) {
  .app-title {
    font-size: 2rem;
  }
  
  .app-subtitle {
    font-size: 1rem;
  }
  
  .info-section {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .primary-btn {
    width: auto;
    padding: 0.75rem 2rem;
  }
}

@media (max-width: 480px) {
  .attendance-card,
  .announcement-card,
  .info-card {
    padding: 1rem;
  }
  
  .app-header {
    padding: 1rem;
  }
  
  .app-title {
    font-size: 1.3rem;
  }
}
</style>